<template>
  <van-popup v-model="show" position="bottom">
    <van-datetime-picker v-model="currentDate" :type="type" @confirm="sure" @cancel="cancel"></van-datetime-picker>
  </van-popup>
</template>
<script>
import { Popup, DatetimePicker } from 'vant'

export default {
  name: 'datePickerPopup',
  components: {
    [Popup.name]: Popup,
    [DatetimePicker.name]: DatetimePicker
  },
  props: {
    // date time year- month month- day datehour
    type: {
      type: String,
      default: 'date'
    }
  },
  data () {
    return {
      show: false,
      currentDate: new Date()
    }
  },
  methods: {
    showPopup () {
      this.show = !this.show
    },
    cancel () {
      this.show = false
    },
    sure (dt) {
      this.show = false
      const date = new Date(dt)
      const y = date.getFullYear()
      let m = date.getMonth() + 1
      m = m < 10 ? ('0' + m) : m
      let d = date.getDate()
      d = d < 10 ? ('0' + d) : d
      let time = ''
      if (this.type === 'date') {
        time = y + '-' + m + '-' + d
      } else if (this.type === 'year-month') {
        time = y + '-' + m
      }

      this.$emit('confirm', time)
    }
  },
  mounted () {

  }
}
</script>
<style lang="scss" scoped>

</style>
